.Searchbar {
    top: 0;
    left: 0;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  }

  .SearchForm {
    display: inline-block;
  }
  
  .SearchForm_button {
    border: none;
    width: 100px;
    height: 30px;
    display: inline-block;
    margin-left: 15px;
    font-weight: 500;
    max-width: 180px;
    padding: 0;
}

.SearchForm_input {
    padding: 5px;
    margin-left: 30px;
    border-radius: 3px;
}